import React from "react";
import { Container, Row, Col, Button } from 'reactstrap';
import {
    Card, CardImg, CardText, CardBody, CardLink,
    CardTitle, CardSubtitle
  } from 'reactstrap';
  import Lottie from 'react-lottie';
  import animationData from '../assets/img/register-success.json'

import * as firebase from 'firebase';

export default class ShopUserRegisterSuccess extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            dataCart:[],
            isStopped: false, 
            isPaused: false,
            speed: 1.5,
        };

        this.ok = this.ok.bind(this);
        
    }

    componentDidMount(){
        const dataCart = firebase.database().ref('cartweb/id');
    
        dataCart.on('value', (snapshot) => {
          let freBaseData = snapshot.val();
          let dataStorageCart = [];    
          snapshot.forEach(productSnapshot => {
            let data = productSnapshot.val();
            dataStorageCart.push({
              keyid:productSnapshot.key,
              name_product:data.name_product,
              details_product:data.details_product,
              price_product:data.price_product,
              profile_picture:data.profile_picture,
              });
        });

          this.setState({
            dataCart: dataStorageCart
          });
        });

        
      }

      ok() {
          this.props.history.push('/shop')
      }


    render() {
        const defaultOptions = {
            loop: false,
            autoplay: true, 
            animationData: animationData,
            rendererSettings: {
              preserveAspectRatio: 'xMidYMid slice'
            }
        };
        return (
            <Container>
                 <Lottie options={defaultOptions}
                    height={150}
                    width={150}
                    speed={this.state.speed}
                    isStopped={this.state.isStopped}
                    isPaused={this.state.isPaused}/>
                    <Button onClick={this.ok}>ตกลง</Button>
            </Container>

        )
    }
}